<template>
    <div class="page-btns">
        <button @click="sub">上一页</button>
        <div>{{ page }} / {{ pageMax }}</div>
        <button @click="add">下一页</button>
    </div>
</template>

<script>
export default {
    name: "PageBtn",

    props: ["page", "pageMax", "set_page"],

    methods: {
        add() {
            this.set_page(Math.min(this.page + 1, this.pageMax));
        },
        sub() {
            this.set_page(Math.max(this.page - 1, 0));
        },
    },
};
</script>

<style scoped>
.page-btns {
    height: 50px;
    display: flex;
    align-items: center;
}
div {
    margin: 0 10px;
}
</style>