<template>
    <div id="app">
        <Tool
            :set_painter_code="set_painter_code"
            :paint_clear="clear_page"
            :random="random"
            :show="showChinese"
            :name="name"
            :painterCode="painterCode"
            :set_random="set_random"
            :set_show="set_showChinese"
            :set_name="set_name"
        />
        <div class="main">
            <div>
                <p>[z]切换显示 [r]切换顺序 [a][d][←][→]翻页</p>
                <p>设备页面宽度: {{ width }}</p>
            </div>

            <PageBtn :page="page" :pageMax="pageMax" :set_page="set_page" />

            <div class="words">
                <WordContainer
                    :words="bin[name][page][0]"
                    :showChinese="showChinese"
                    :random="random"
                    :paint="paint"
                />
                <WordContainer
                    :words="bin[name][page][1]"
                    :showChinese="showChinese"
                    :random="random"
                    :paint="paint"
                />
            </div>

            <PageBtn :page="page" :pageMax="pageMax" :set_page="set_page" />
        </div>
    </div>
</template>

<script>
import PageBtn from "@/components/PageBtn";
import Tool from "@/components/Tool";
import WordContainer from "@/components/WordContainer";
export default {
    name: "App",
    data() {
        return {
            width: 0,

            name: "",
            page: 0,

            showChinese: true,
            random: false,

            painterCode: 0,

            bin: {},
        };
    },
    components: { PageBtn, Tool, WordContainer },
    created() {
        this.name = "四级词汇";
        this.page = 0;

        // 临时函数
        var deal = function (temp) {
            var block = [];
            // 按页遍历
            temp.forEach((page) => {
                var n_page = [];
                // 按列遍历
                page.forEach((column) => {
                    var n_column = [];
                    // 遍历所有属性
                    var keys = Object.keys(column);
                    keys.forEach((key) => {
                        // 将每个对象都拆解为key,value
                        var value = column[key];
                        // 重组为新的对象
                        var item = {
                            key: key,
                            val: value,
                            group: 0,
                        };
                        // 加入到新列
                        n_column.push(item);
                    });
                    // 加入到新页
                    n_page.push(n_column);
                });
                // 加入到新block
                block.push(n_page);
            });
            return block;
        };

        // 初始化单词池
        this.bin = {};
        var temp;
        // 加入到bin里
        temp = require("./1.json");
        this.bin[temp["name"]] = deal(temp["words"]);
        temp = require("./2.json");
        this.bin[temp["name"]] = deal(temp["words"]);
        temp = require("./3.json");
        this.bin[temp["name"]] = deal(temp["words"]);

        // 读取本地缓存
        Object.keys(this.bin).forEach((key) => {
            temp = localStorage.getItem(key);
            if (temp) this.bin[key] = JSON.parse(temp);
        });
        temp = localStorage.getItem("setting");
        if (temp) {
            temp = JSON.parse(temp);
            this.name = temp["name"];
            this.showChinese = temp["show"];
            this.page = temp["page"];
            this.random = temp["random"];
        }

        if (!this.bin["四级收藏"]) {
            this.cal_red();
        }
    },
    mounted() {
        document.addEventListener("keyup", this.keyupEvent);
        window.onresize = () => {
            return (() => {
                this.width = document.body.clientWidth;
            })();
        };
        this.width = document.body.clientWidth;
    },
    destroyed() {
        document.removeEventListener("keyup", this.keyupEvent);
    },
    computed: {
        pageMax() {
            return this.bin[this.name].length - 1;
        },
    },
    methods: {
        keyupEvent(event) {
            if (event.key == "z") {
                this.showChinese = !this.showChinese;
            } else if (event.key == "a" || event.key == "ArrowLeft") {
                this.page = Math.max(0, this.page - 1);
            } else if (event.key == "d" || event.key == "ArrowRight") {
                this.page = Math.min(this.pageMax, this.page + 1);
            } else if (event.key == "r") {
                this.random = !this.random;
            } else if (event.key == "1") {
                this.painterCode = this.painterCode == 1 ? 0 : 1;
            } else if (event.key == "2") {
                this.painterCode = this.painterCode == 2 ? 0 : 2;
            } else if (event.key == "3") {
                this.painterCode = this.painterCode == 3 ? 0 : 3;
            }
        },
        upload_words() {
            localStorage.setItem(
                this.name,
                JSON.stringify(this.bin[this.name])
            );
            if (this.name == "四级收藏") {
                localStorage.setItem(
                    "四级词汇",
                    JSON.stringify(this.bin["四级词汇"])
                );
            }
            console.log("保存到本地缓存");
        },
        clear_page() {
            this.bin[this.name][this.page].forEach((e) =>
                e.forEach((e) => (e.group = 0))
            );
            // 强制更新全部
            this.$forceUpdate();
            this.$children.forEach((e) => e.$forceUpdate());
            this.upload_words();
        },
        paint(item) {
            if (this.painterCode == 0) return;

            if (item.group == this.painterCode) item.group = 0;
            else item.group = this.painterCode;

            // 强制更新全部
            this.$forceUpdate();
            this.$children.forEach((e) => e.$forceUpdate());
            this.upload_words();
        },

        upload_setting() {
            var setting = {
                name: this.name,
                page: this.page,
                random: this.random,
                show: this.showChinese,
            };
            localStorage.setItem("setting", JSON.stringify(setting));
        },

        cal_red() {
            // 计算收藏
            this.bin["四级收藏"] = [];
            // 一页40个，一列20个
            let cnt = 0;
            this.bin["四级词汇"].forEach((page) => {
                page.forEach((column) => {
                    column.forEach((item) => {
                        if (item.group == 1) {
                            // 新页
                            if (cnt % 40 == 0) this.bin["四级收藏"].push([]);
                            // 新列
                            if (cnt % 20 == 0)
                                this.bin["四级收藏"][Math.floor(cnt / 40)].push(
                                    []
                                );
                            this.bin["四级收藏"][Math.floor(cnt / 40)][
                                Math.floor((cnt % 40) / 20)
                            ].push(item);
                            cnt++;
                        }
                    });
                });
            });
        },

        set_painter_code(val) {
            this.painterCode = val;
        },
        set_page(val) {
            this.page = val;
            this.upload_setting();
        },
        set_random(val) {
            this.random = val;
            this.upload_setting();
        },
        set_showChinese(val) {
            this.showChinese = val;
            this.upload_setting();
        },
        set_name(val) {
            this.name = val;
            this.page = 0;
            this.upload_setting();
            if (val == "四级收藏") this.cal_red();
        },
    },
};
</script>

<style>
#app {
    font-family: Avenir, Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: #2c3e50;
    margin-top: 60px;
    display: flex;
    justify-content: center;
}
.main {
    display: grid;
    grid-template-rows: repeat(4, auto);
    width: auto;
    justify-items: center;
    align-items: center;
}

.words {
    display: flex;
    flex-wrap: wrap; /* 方便手机端适配 */
    justify-content: center;
}
</style>
