<template>
    <div class="container">
        <button class="func-btn" @click="back_top">
            <a @click.stop href="#top">回到顶部</a>
        </button>
        <button class="func-btn" @click="switch_name">
            {{ name }}
        </button>
        <button class="func-btn" @click="set_random(!random)">
            {{ randomStatus }}
        </button>
        <button class="func-btn" @click="set_show(!show)">
            {{ showStatus }}
        </button>
        <button class="func-btn" @click="painterShow = !painterShow">
            {{ painterStatus }}
        </button>
        <div class="painter-container" v-show="painterShow">
            <button
                v-for="(item, index) in painters"
                :key="index"
                :class="
                    'painter ' +
                    item.color +
                    (painterCode == item.code ? ' show' : '')
                "
                @click="set_code(item.code)"
            >
                {{ item.name }}
            </button>
        </div>
    </div>
</template>

<script>
export default {
    name: "Tool",

    data() {
        return {
            painters: [
                {
                    color: "red",
                    name: "红",
                    code: 1,
                },
                {
                    color: "blue",
                    name: "蓝",
                    code: 2,
                },
                {
                    color: "green",
                    name: "绿",
                    code: 3,
                },
                {
                    color: "white",
                    name: "清除本页",
                    code: 0,
                },
            ],
            painterShow: true,
        };
    },

    props: [
        "set_painter_code",
        "paint_clear",
        "painterCode",
        "random",
        "show",
        "name",
        "set_random",
        "set_show",
        "set_name",
    ],

    methods: {
        set_code(val) {
            // 点击清空
            if (val == 0) this.paint_clear();
            // 重复点击，按钮关闭
            else if (this.painterCode == val) val = 0;

            this.set_painter_code(val);
        },
        switch_name() {
            var a;
            if (this.name == "四级词汇") a = "初中词汇";
            else if (this.name == "初中词汇") a = "高中词汇";
            else if (this.name == "高中词汇") a = "四级收藏";
            else a = "四级词汇";
            this.set_name(a);
        },
        back_top(event) {
            event.target.children.item(0).click();
        },
    },
    computed: {
        randomStatus() {
            return this.random ? "乱序" : "顺序";
        },
        showStatus() {
            return this.show ? "中英" : "纯英";
        },
        painterStatus() {
            return this.painterShow ? "打开" : "关闭";
        },
    },
};
</script>

<style scoped>
.container {
    position: fixed;
    right: 10px;
    display: flex;
    flex-direction: column;
    bottom: 10%;
    width: 70px;
}

.func-btn {
    height: 40px;
}

.painter-container {
    position: relative;
    display: flex;
    flex-direction: column;
}

.painter {
    height: 70px;
    width: 70px;
    font-size: 20px;
    border: solid 1px;
    opacity: 0.3;
}

.show {
    opacity: 1;
}

.red {
    color: white;
    background-color: red;
}
.blue {
    color: white;
    background-color: blue;
}
.green {
    color: white;
    background-color: green;
}
.white {
    background-color: white;
}
button > a {
    text-decoration-line: none;
    color: inherit;
}
</style>