<template>
    <div>
        <div
            class="word"
            v-for="(item, index) in showWords"
            :key="index"
            @click="paint(item)"
            :class="'color' + item.group"
        >
            <div>
                <a
                    :href="'https://fanyi.baidu.com/#en/zh/' + item.key"
                    target="view_window"
                    @click.stop
                >
                    {{ item.key }}
                </a>
            </div>
            <div v-show="showChinese" :class="longCheck(item.val)">
                {{ item.val }}
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: "WordContainer",
    props: ["words", "showChinese", "paint", "random"],
    methods: {
        shuffle(arr) {
            var len = arr.length;
            for (var i = 0; i < len - 1; i++) {
                var index = parseInt(Math.random() * (len - i));
                var temp = arr[index];
                arr[index] = arr[len - i - 1];
                arr[len - i - 1] = temp;
            }
            return arr;
        },
        longCheck(val) {
            if (val.length > 20) return "long";
            else return "";
        },
    },
    computed: {
        showWords() {
            var a = this.words;
            if (this.random) {
                var b = [];
                a.forEach((e) => b.push(e));
                return this.shuffle(b);
            }
            return a;
        },
    },
};
</script>

<style scoped>
.word {
    display: flex;
    width: 360px;
    height: 30px;
    align-items: center;
    justify-content: space-between;
    padding: 8px;
    margin: 1px;
    border: solid gray 1px;
}

.long {
    font-size: 12px;
    width: 300px;
    text-align-last: right;
}

.color1 {
    background-color: lightpink;
}
.color2 {
    background-color: lightblue;
}
.color3 {
    background-color: lightgreen;
}
</style>